import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { PageMetaData } from '@core/models';
import { CommonUtil } from '@core/utils/common.util';

@Component({
    selector: 'findqo-pagination',
    templateUrl: './pagination.component.html',
    styleUrls: ['./pagination.component.scss'],
    standalone: false
})
export class PaginationComponent implements OnChanges, OnInit {
	@Input() pageMetaData: PageMetaData;

	perPage: number;
	totalPages: number;
	currentPage: number;
	total: number;
	pages: number[] = [];

	private updatePageNumbers(): void {
		this.pages = [];
		let startPage = Math.max(1, this.currentPage - 2);
		const endPage = Math.min(this.totalPages, startPage + 4);

		// Adjust startPage if we're near the end to always show 5 pages
		if (endPage - startPage < 4) {
			const adjustment = 4 - (endPage - startPage);
			if (startPage - adjustment > 0) {
				startPage -= adjustment;
			}
		}

		for (let i = startPage; i <= endPage; i++) {
			this.pages.push(i);
		}
	}

	private setPageMetaData(pageMetaData: PageMetaData): void {
		if (pageMetaData) {
			this.currentPage = Number.parseInt(pageMetaData?.currentPage);
			this.totalPages = Number.parseInt(pageMetaData?.totalPages);
			this.total = Number.parseInt(pageMetaData?.total);
			this.perPage = Number.parseInt(pageMetaData?.perPage);
			this.totalPages = Math.ceil(this.total / this.perPage);
			this.updatePageNumbers();
		}
	}

	ngOnInit(): void {
		this.setPageMetaData(this.pageMetaData);
	}

	ngOnChanges(changes: SimpleChanges): void {
		CommonUtil.handleNgOnChanges(changes['pageMetaData'], (pageMetaData: PageMetaData) => {
			this.setPageMetaData(pageMetaData);
		});
	}
}
